<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>

		<b-form>

			<b-form-group label="*Fecha" label-for="fecharealizacion">
				<b-form-input id="fecharealizacion"
							  v-model="devoluciones.fecharealizacion"
							  type="date"
							  :disabled="stockhabilitado"
							  placeholder="Ingrese la fecha de realizacion de la devolucion"
							  invalid-feedback="Complete este campo"
							  :state="validacion.fecharealizacion.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="fecharealizacion-live-feedback">
					{{ validacion.fecharealizacion.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Tipo de Devolucion"
						  label-for="tipo_dev"
						  @submit.stop.prevent="handleSubmit">
				<b-form-select id="tipo_dev"
							   v-model="devoluciones.tipo_dev"
							   type="text"
							   @change="cambiodetipo()"
							   placeholder="Describa el tipo de devolucion"
							   invalid-feedback="Complete este campo"
							   :state="validacion.tipo_dev.estado"
							   required
							   :options="tipos_productos">
				</b-form-select>
				<b-form-invalid-feedback id="tipo_dev-live-feedback">
					{{ validacion.tipo_dev.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Cliente"
						  label-for="id_cliente"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_cliente"
								:state="validacion.id_cliente.estado"
								v-model="devoluciones.id_cliente"
								type="text"
								placeholder="Ingrese un cliente"
								invalid-feedback="Complete este campo"
								required
								solo
								filled
								:items="op_clientes">
				</v-autocomplete>
				<b-form-invalid-feedback id="id_cliente-live-feedback">
					{{ validacion.id_cliente.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Stock"
						  label-for="id_stock"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_stock"
								:state="validacion.id_stock.estado"
								v-model="devoluciones.id_stock"
								type="text"
								placeholder="Ingrese un stock"
								invalid-feedback="Complete este campo"
								required
								solo
								:disabled="stockhabilitado"
								filled
								:items="op_stocks"
								@change="setStock">
				</v-autocomplete>
				<b-form-invalid-feedback id="id_stock-live-feedback">
					{{ validacion.id_stock.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<!----------    Beneficios         -------------->
			<div v-show="btn_producto" v-for="(item, index) in devoluciones.componentes" :key="index">
				<b-form>
					<div class="d-flex  justify-content-between">

						<h4>Producto: {{ index + 1 }}</h4>
						<b-button variant="danger"
								  @click="eliminarProducto(index)"
								  style="color: white">
							<v-icon style="color: white"> mdi-delete </v-icon>
						</b-button>
					</div>
					<b-form-group label="*Producto"
								  label-for="producto"
								  @submit.stop.prevent="handleSubmit">
						<v-autocomplete id="producto"
										:state="validacion.componentes[index].producto.estado"
										v-model="item.producto"
										type="text"
										placeholder="Ingrese un Numero"
										invalid-feedback="Complete este campo"
										required
										solo
										@change="cambiarUnitario(index)"
										filled
										:items="op_productos">
						</v-autocomplete>
						<b-form-invalid-feedback id="producto-live-feedback">
							{{ validacion.componentes[index].producto.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Cantidad Devuelta" label-for="cantidad">
						<b-form-input id="cantidad"
									  :state="validacion.componentes[index].cantidad.estado"
									  v-model="item.cantidad"
									  type="number"
									  placeholder="*Ingrese la cantidad"
									  invalid-feedback="Complete este campo"
									  required
									  v-on:keyup="sumar(item.cantidad, item.precio);">
						</b-form-input>
						<b-form-invalid-feedback id="cantidad-live-feedback">
							{{ validacion.componentes[index].cantidad.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Importe Reconocido" label-for="precio">
						<span style="display:inline-bk;width:3%;">$</span>
						<b-form-input id="precio"
									  :state="validacion.componentes[index].precio.estado"
									  v-model="item.precio"
									  type="number"
									  style="width:97%;display:inline-bk;"
									  placeholder="*Ingrese el precio"
									  invalid-feedback="Complete este campo"
									  required
									  v-on:keyup="sumar(item.cantidad, item.precio, index)">
						</b-form-input>
						<b-form-invalid-feedback id="precio-live-feedback">
							{{ validacion.componentes[index].precio.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<span>Total Producto: $</span> {{item.cantidad * item.precio}}
				</b-form>
				<b-button class="my-3"
						  variant="success"
						  @click="sumarProductos()"
						  style="color: white">+</b-button>
			</div>

			<br><br><span style="text-align:center;width:100%;">Total devolucion: ${{totalpago}}</span>

		</b-form>

		<b-button class="mt-2" variant="success" bk @click="postdevolucion()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
    import axios from "axios";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				devoluciones: {componentes:[]},
				data: {},
				list_productos: {},
				listacli: [],
                productoelegido: {},
				text: "",
				totalprod: [0],
				totalpago: 0,
                stockviejo: [],
				numero: -1,
                stockhabilitado: false,
				stockusado: {},
				op_productos: [],
				op_clientes: [],
				op_stocks: [],
				productonuevo: {},
                tipos_productos: [
                    { value: null, text: "Elija un tipo de producto", disabled: true },
                    { value: "Pollo", text: "Pollo" },
                    { value: "Congelados", text: "Congelado" }
                ],
				listaproduct: [],
                listastock: [],
				validacion: {
					fecharealizacion: { estado: null, mensaje: "" },
					tipo_dev: {estado: null, mensaje: "" },
					id_cliente: { estado: null, mensaje: "" },
                    id_stock: { estado: null, mensaje: "" },
                    componentes: [],
				},
			};
		},

		methods: {

			async postdevolucion() {
				let devolucionesAPI = new APIControler();
				devolucionesAPI.apiUrl.pathname = "devoluciones/";
				console.log(this.devoluciones.id_stock.split('/')[4]);
				this.actualizarStockProducto();
				let respuesta = await devolucionesAPI.postData(this.devoluciones);
				this.cargarFeedback(respuesta);
				this.updateTable();
			},
			getProducto(id) {
				let producto = this.listaproduct.find(s => {
					return s.id_producto == id
				})
				return producto;
			},
			async actualizarStockProducto() {
				let stockfinal = this.getStock(this.devoluciones.id_stock.split('/')[4])
				console.log("Hola");
				console.log(stockfinal);
                this.stockusado.nombre = stockfinal.nombre;
                this.stockusado.id_reparto = stockfinal.id_reparto;
                this.stockusado.fechastock = stockfinal.fechastock;
                this.stockusado.componentes = stockfinal.componentes;
                this.devoluciones.componentes.forEach((produc) => {
					let id = produc.producto.split('/')[4];
					if (!this.checkprodstock(id)) {
						this.productonuevo.producto = produc.producto;
						this.productonuevo.cantidad = produc.cantidad;
						this.stockusado.componentes.push(this.productonuevo);
					} else
					{
                        this.stockusado.componentes.forEach((compon) => {
                            if (compon.producto.split('/')[4] == id) {
                                compon.cantidad = parseInt(compon.cantidad) + parseInt(produc.cantidad);
                            }
                        });
					}
                    console.log("Id del producto en venta:", id);
				});
				console.log(this.stockusado);
                await axios
                    .put(
                        "http://alhost:8081/stocks/" +
                        stockfinal.id_stock +
                        "/",
                        this.stockusado
                    );
			},
            checkprodstock2(id_producto) {
                let cantidad = null;
                if (this.stockusado != {}) {
                    for (var i = 0; i < this.stockusado.componentes.length; i++) {
                        if (this.stockusado.componentes[i].producto.split('/')[4] == id_producto) {
                            cantidad = this.stockusado.componentes[i].cantidad;
                        }
                    }
                }
                return cantidad;
			},
            cambiodetipo() {
                this.getClientes();
                this.getProductos();
            },
			getStock(id) {
				id = parseInt(id);
				console.log(this.listastock);
                let stock = this.listastock.find(s => {
                    return s.id_stock == id
                })
                return stock;
			},
            checkprodstock(id_producto) {
                let cantidad = false;
                if (this.stockusado != {}) {
                    for (var i = 0; i < this.stockusado.componentes.length; i++) {
                        if (this.stockusado.componentes[i].producto.split('/')[4] == id_producto) {
							cantidad = true;
                        }
                    }
                }
                return cantidad;
            },
            cambiarUnitario(index) {
				this.devoluciones.componentes[index].precio = this.getProducto(this.devoluciones.componentes[index].producto.split('/')[4]).precioref
				this.calctotalpago();
            },
			sumar(valor1, valor2, index) {
				var total = 0;
				valor1 = parseInt(valor1);
				valor2 = parseInt(valor2);// Convertir el valor a un entero (número).
				// Aquí valido si hay un valor previo, si no hay datos, le pongo un cero "0".
				//total = (total == null || total == undefined || total == "") ? 0 : total;
				/* Esta es la suma. */
				total = (parseInt(valor1) * parseInt(valor2));
				// Coar el resultado de la suma en el control "span".
				(this.totalprod)[index] = total;
				this.totalprod.push(0);
				this.totalprod.pop();
				this.calctotalpago();
			},
			calctotalpago() {
                let total = 0;
                for (var i = 0; i < this.devoluciones.componentes.length; i++) {
                    if (!isNaN((this.devoluciones.componentes)[i].precio) && !isNaN((this.devoluciones.componentes)[i].cantidad)) {
                        total = total + ((this.devoluciones.componentes)[i].precio * (this.devoluciones.componentes)[i].cantidad);
                    }
                }
                console.log(total);
                this.totalpago = total;
			},
			async handleInput() {
				this.listastock = [];
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "stocks/";
                this.listastock = await productosAPI.getData();
				var today = new Date();
				today.setDate(today.getDate());
				var date = today.getFullYear() + "-" + ('0' + (today.getMonth() + 1)).slice(-2) + "-" + ('0' + today.getDate()).slice(-2);
				this.devoluciones.fecharealizacion = date;
                this.devoluciones.tipo_dev = 'Pollo';
				console.log(String(date));
				if (this.$session.exists()) {
					if (this.$session.get('tipocuenta') == 'reparto')
					{
                        this.devoluciones.id_stock = "http://alhost:8081/stocks/" + this.$session.get('stockused').id_stock + "/"
                        this.stockhabilitado = true;
						this.getClientesRep();
					}
					this.stockusado = this.getStock(this.$session.get('stockused').id_stock);
					this.getProductos();
                }
			},
			async getClientesRep() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "clientes/";
                this.listacli = await productosAPI.getData();
				this.op_clientes = [];
				let option = {}
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://alhost:8081/clientes/" + element.numero_cliente + "/";
                    option.text = element.nombre;
                    if (this.$session.get('repartused').clientes_reparto.includes(option.value)) {
                        this.op_clientes.push(option);
                    }
                });
            },
			async resetForm() {
				this.devoluciones.fecharealizacion = "";
				this.devoluciones.componentes = null;
			},
			//Obtengo los productos
            async getProductos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "productos/";
                this.listaproduct = await productosAPI.getData();
                this.op_productos = [];

                let option

                this.listaproduct.forEach((element) => {
                    option = {};
                    option.value =
                        "http://alhost:8081/productos/" + element.id_producto + "/";
                    option.text = element.nombre + ' - Precio Ref: $' + element.precioref;
                    if (this.devoluciones.id_stock == "general") {
                        option.text = option.text + '- Stock: ' + element.stocktotal
                        if (element.tipo_producto == this.devoluciones.tipo_dev) {
                            this.op_productos.push(option);
                        }
                    } else {
                        if (this.checkprodstock2(element.id_producto) == null) {
                            option.text = option.text + ' - no esta en stock';
                        } else {
                            option.text = option.text + ' - Stock: ' + this.checkprodstock2(element.id_producto)
                        }
                        console.log(option);
                    }
                    if (element.tipo_producto == this.devoluciones.tipo_dev) {
                        this.op_productos.push(option);
                    }
                });
            },
            setStock() {
                console.log(this.devoluciones.id_stock)
                if (this.devoluciones.id_stock != "general") {
                    this.stockusado = this.getStock(this.devoluciones.id_stock.split('/')[4]);
                    console.log(this.stockusado)
                }
                this.getProductos();
            },
            async getStocks() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "stocks/";
				this.listastock = await productosAPI.getData();
				let option
                this.listastock.forEach((element) => {
                    option = {};
                    option.value =
                        "http://alhost:8081/stocks/" + element.id_stock + "/";
                    option.text = 'Id Stock: ' + element.id_stock + ' - Reparto: ' + element.id_reparto.split('/')[4] + ' - Fecha: ' + element.fechastock;
					console.log(option);
					if (element.cerrado == false)
					{
                        this.op_stocks.push(option);
					}
				});
            },
            async getClientes() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "clientes/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://alhost:8081/clientes/" + element.numero_cliente + "/";
                    option.text = element.nombre;
                    console.log(option);
                    this.op_clientes.push(option);
                });
            },
			getFechaPrecio(id)
			{
                let fecha = "Nunca se compro";
				let fechaelemento = new Date(2000,1,1);
				let ultimoprecio = 0;
				console.log(this.listadevolucion);
				this.listadevolucion.forEach((element) => {
					fechaelemento = new Date(element.fecharealizacion)
					element.componentes.forEach((compon) =>
					{
                        if (compon.producto.split('/')[4] == id)
						{
							ultimoprecio = compon.precio;
                            fecha = (fechaelemento.getUTCDate()) + "/" + (fechaelemento.getMonth() + 1) + "/" + (fechaelemento.getUTCFullYear());
						}
					});
				});
				return ('Ultimo precio:' + ultimoprecio + ' - Fecha:' + fecha);
			},
            async sumarProductos() {
				this.btn_producto = true;
				this.numero = this.numero + 1;
				// this.resetFormAdh();
				// this.cantidad=this.cantidad +1;
				this.devoluciones.componentes.push({
					cantidad: 0,
					producto: null,
					precio: 0,
				});
                this.validacion.componentes.push({
					cantidad: { estado: null, mensaje: "" },
					producto: { estado: null, mensaje: "" },
                    precio: { estado: null, mensaje: "" },
				});
            },
			async eliminarProducto(index) {
                if (this.devoluciones.componentes.length == 1) {
					alert("Tiene que haber al menos 1 producto");
				} else {
					this.devoluciones.componentes.splice(index, 1)
					this.validacion.componentes.splice(index, 1)
					this.totalprod.splice(index);
					this.numero = this.numero - 1;
                    this.calctotalpago();
				}
            },
			cargarFeedback(respuesta) {
				let valido;
				if (!respuesta) respuesta = {};
				for (let key in this.validacion) {
					valido = !respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					if (!valido) this.validacion[key].mensaje = respuesta[key][0];
				}
                this.cargarFeedbackcomponentes(respuesta)
			},
            cargarFeedbackcomponentes(respuesta) {
				let valido;
                let ok=false
                if (!respuesta.componentes) ok=true;
                this.validacion.componentes.forEach((b,index )=> {
                    for (let key in b) {
                        valido = ok || !respuesta.componentes[index].hasOwnProperty(key);
                        b[key].estado = valido;
                        console.log(key);
                        if (!valido) b[key].mensaje = respuesta.componentes[index][key][0];
                    }
                });
				
			},
		},
		beforeMount() {
            this.getStocks()
            this.getClientes()
			this.sumarProductos()
			this.handleInput()
            this.getProductos()
		},
		/*
  computed: {
      validation() {
        return this.text.length > 0 ? true : false;
      },
    }
  */
	};
</script>

<style></style>
